.review-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.review-card {
  background-color: #ffffff;
  color: #111;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.2);
}

.review-header {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
  gap: 0.8rem;
}

.review-score {
  background-color: #3cb371;
  color: #fff;
  font-weight: bold;
  font-size: 18px;
  padding: 0.5rem 0.7rem;
  border-radius: 6px;
  min-width: 40px;
  text-align: center;
}

.review-date {
  font-size: 0.75rem;
  color: #666;
}

.review-content {
  font-size: 0.95rem;
  line-height: 1.4;
}
