/* Header.css */
.header {
  background-color: #000;
  padding: 1rem 2rem;
  color: white;
}

.header-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  text-decoration: none;
  color: white;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-size: 1rem;
}

.nav-links a:hover {
  color: #f5c518;
}

.register-button {
  background-color: #f5c518;
  color: black;
  font-weight: bold;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
}

.register-button:hover {
  background-color: #e0b000;
}

.user-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.user-actions a,
.user-actions button {
  font-size: 1rem;
  color: white;
  background: none;
  border: none;
  cursor: pointer;
}

.user-actions a:hover,
.user-actions button:hover {
  color: #f5c518;
}
