/* src/pages/Home/Home.css */
.home {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.home h2 {
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  color: white;
}

.game-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1.5rem;
}
